@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "muli", sans-serif;
}



.navbar{background-color:#fff !important}.navbar .navbar-brand{font-size:1.9rem !important;color:#578753 !important}.navbar a{font-size:1.3rem;text-transform:capitalize}.navbar a:hover{color:#f0e00a !important}.navbar .menu_active{font-weight:bold;border-bottom:1px solid #565387}@media (max-width: 991px){.navbar-brand{font-size:1.5rem !important}.menu_active{font-weight:bold;border-bottom:none}}#header{width:100%;height:80vh;margin-top:70px}#header h1{margin:0 0 10px 0;font-size:48px;font-weight:700;color:#000}#header .brand-name{color:#3498db}#header h2{color:#484848}#header .btn-get-started{font-weight:500;font-size:16px;letter-spacing:1px;display:inline-block;padding:10px 30px;border-radius:50px;transition:0.5s;color:#3498db;border:2px solid #3498db;text-decoration:none}#header .btn-get-started:hover{background:#3498db;color:#fff}#header .animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}#header-img{text-align:right}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}p{text-align:center;color:red}

.userlist{margin:1rem;padding:1rem;background-color:#5f0e08;box-shadow:0 2px 8px rgba(0,0,0,0.26);border-radius:12px;text-align:center;color:white}.userlist h2{font-size:2rem;color:#f7e702}.userlist h3{color:#eccf77;margin:0;font-size:1rem}.userlist h5{color:#773108;margin:0}.userlist p{color:#0ca507;margin:0}

.user-list{list-style:none;margin:0;padding:0}

.sec{margin:1rem auto;width:90%;max-width:40rem;text-align:center;background-color:white;padding:2rem;border-radius:12px}button{font:inherit;cursor:pointer;background:#d49611;border:1px solid #d4ab22;color:white;padding:0.75rem 2rem;border-radius:20px}button:focus{outline:none}button:hover,button:active{background-color:#460897;border-color:#460897}

